function reducer(state,action){

    if(action.type === 'SEARCHED'){
        return {
            ...state,
            booksToShow: action.books,
            isTyping:action.typingState,
        }
    }else if(action.type === 'PUBLISHER_PICKED'){
        return {
            ...state,
            booksToShow:action.books,
            filteredLibrary:action.books,
            isPublisherPicked:action.filterState,
        }
    }else if(action.type === 'SORTED'){
        return {
            ...state,
            booksToShow: action.books,
            filteredLibrary:action.books,
        }
    }else if(action.type === 'INPUT_EMPTY'){
        return {
            ...state, 
            isTyping:action.typingState,
        }
}else if(action.type === 'ALL_LOADED'){
    return {
        ...state, 
        isTyping:false,
        booksToShow: action.books,
        filteredLibrary:null,
        isPublisherPicked:false,
    }
}else if(action.type === 'BOOK_WAS_PICKED'){
    return  {
        ...state,
        isBookPicked:true,
        pickedBook:action.pickedBook,
    }
}else if(action.type === 'BOOK_WAS_ADDED_TO_BIN'){
    return  {
        ...state,
        booksInBin:action.booksInBin,
        btnsIds:action.btnsIds,
    }
}else if(action.type === 'BOOK_WAS_DELETED_FROM_BIN'){
    return  {
        ...state,
        booksInBin:action.booksInBin,
        btnsIds:action.btnsIds,
    }
}else if(action.type === 'SENT_FORCE_UPDATE_FUNC'){
    return  {
        ...state,
        forceUpdateHeaderFunc:action.forceUpdateHeaderFunc,
    }
}else if(action.type === 'USERS_RECEIVED'){
    return  {
        ...state,
        users:action.users,
    }
}else if(action.type === 'BOOK_WAS_COMMENTED'){
    return  {
        ...state,
        comments:action.comments,
    }
}else{
        return state;
    }
    
}

export default reducer;