import BookCard from './BookCard';
import {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import '../css/booksContainer.scss';
import { nanoid } from 'nanoid';
import $ from 'jquery';







function BooksContainer(props){
    
    const [value, setValue] = useState(0); // integer state

    function useForceUpdate(){
        setValue(value + 1); // update the state to force render
    }

    props.getFuncFrom(useForceUpdate);

    useEffect(()=>{
      
        let btns=document.querySelectorAll('.to-bin_btn');
        if(props.btnIds.length!=0){
        btns.forEach((item)=>{
            props.btnIds.forEach((id)=>{
                if(item.dataset.id==+id){
                    $(item).text('добавлено').addClass('disabled').attr('disabled',true);
                };
            })
            
        })}
            
    })

    return(

        <ul className="books-container">
            {
                
                props.booksToShow.map((book)=>{
                    return <BookCard book={book} key={nanoid()} bookid={book.id}/>
                })
            }
                
        </ul>     

    )
}

function mapStateToProps(state){
    return {
        booksToShow:state.booksToShow,
        filteredLibrary:state.filteredLibrary,
        btnIds:state.btnIds,
    }
}

export default connect(mapStateToProps)(BooksContainer);