import React from 'react';
import $ from 'jquery';
import {connect} from 'react-redux';
import '../../css/main.scss';
import Banner from '../Banner';

import BooksContainer from '../BooksContainer';
import Library from '../Library';
import BookPage from '../BookPage';


class Main extends React.Component {

constructor(props) {
  super(props);
  

  this.state = {
    forceUpdateBooksContainer:null,
    loaded: false,
    library:null,
    filter:false,
    result:null,
    isGotFunc:false,  
    };



    
   
    

}//constructor




getFuncFrom=(func)=>{
  this.setState({
    forceUpdateBooksContainer:func,
  })
}
  




colorBlank=(e)=>{

  const blanks=document.getElementsByClassName('m-blank');

  for(let i=0; i<blanks.length; i++){
    blanks[i].classList.remove('active-blank');
  }

  e.target.classList.add('active-blank');

  }

  colorCategory=(e)=>{

    const categories=document.getElementsByClassName('m-category_li');
  
    for(let i=0; i<categories.length; i++){
      categories[i].classList.remove('active-blank');
      $(categories[i]).css('text-align', 'left');
    }
  
    e.target.classList.add('active-blank');
    }



loadSorted=(e)=>{
  this.colorBlank(e);

  let sortBy=e.target.dataset.sortby;
  let isChipest=e.target.dataset.chipest;
  let compare;

  if(isChipest=='true'){
    compare=(a, b)=>{
      if (a[sortBy] > b[sortBy]) return 1; 
      if (a[sortBy] === b[sortBy]) return 0; 
      if (a[sortBy] < b[sortBy]) return -1; 
    }
  }else{
    compare=(a, b)=>{
      if (a[sortBy] < b[sortBy]) return 1; // если первое значение больше второго
      if (a[sortBy] === b[sortBy]) return 0; // если равны
      if (a[sortBy] > b[sortBy]) return -1; // если первое значение меньше второго
  }
}

  let sortedBooks=this.props.booksToShow.sort(compare);


  this.props.dispatch({
    type:'SORTED',
    books:sortedBooks,
  });

  this.state.forceUpdateBooksContainer();
  
}


loadAll=(e)=>{
  this.colorCategory(e);
  const [...copiedLibrary] = Library;
  this.props.dispatch({
   type:'ALL_LOADED',
   books:copiedLibrary,
  })
}


loadPublisher=(e)=>{
  this.colorCategory(e);
  $('#searchInput').val('');

  let publisher=e.target.dataset.publisher;

  let filteredBooks=Library.filter(item=>item.publisher===publisher);
 
    this.props.dispatch({
    type:'PUBLISHER_PICKED',
    books:filteredBooks,
    filterState:true,
    })
 
 
}

 

showSortBlanks=() => {
 let isShowed=false;
  if(!this.isShowed){
    $('.m-blanks_wrapper').css('display','flex');
    $('.m-categories').fadeIn();
    this.isShowed=true;
  }else{
    $('.m-blanks_wrapper').fadeOut();
    $('.m-categories').hide();
    this.isShowed=false;
  }
  
}


componentDidMount() {

}//didMount



render() {   

      return(
  <main className="main">
    {/* <BookPage/> */}
    
    <div className="m-container container">
      <Banner dispatch={this.props.dispatch} booksToShow={this.props.booksToShow}/>
        <div className="m-sections_wrapper">
            <aside className="m-categories">
                <div className="m-categories_wrapper">
                    <h2>Издания</h2>
                    <ul className="m-category-li_wrapper">
                        <li className="m-category_li" onClick={this.loadAll}>ВСЕ</li>
                        <li className="m-category_li" onClick={this.loadPublisher} data-publisher="Marvel">Marvel</li>
                        <li className="m-category_li" onClick={this.loadPublisher} data-publisher="DC">DC</li>
                        <li className="m-category_li" onClick={this.loadPublisher} data-publisher="Cartoon books">Cartoon books</li>
                        <li className="m-category_li" onClick={this.loadPublisher} data-publisher="Dark Horse">Dark Horse</li>
                        <li className="m-category_li" onClick={this.loadPublisher} data-publisher="Image">Image</li>
                        <li className="m-category_li" onClick={this.loadPublisher} data-publisher="Vertigo">Vertigo</li>
                    </ul>
                </div>

            </aside>
            <div className="m-body">
              <div className="m-hidden_btns">
              <p className="hidden-sort-btn" onClick={this.showSortBlanks}>Сортировать</p>
              </div>
            
                <div className="m-blanks_wrapper">
                    
                    <p onClick={this.loadSorted} className="m-blank"  data-sortby="rating">Бестселлеры</p>
                    <p onClick={this.loadSorted} className="m-blank"  data-sortby="year">Новинки</p>
                    <p onClick={this.loadSorted} className="m-blank"  data-sortby="price">Сначала дорогие</p>
                    <p onClick={this.loadSorted} className="m-blank"  data-sortby="price" data-chipest="true">Сначала дешевые</p>
                </div>
                <BooksContainer getFuncFrom={this.getFuncFrom}/>
            </div>
            

        </div>

    </div>
</main>
  );
  }//render
}//class main


function mapStateToProps(state) {
  return {
    booksToShow: state.booksToShow,
    isFiltered:state.isFiltered,
    filteredLibrary:state.filteredLibrary,
    searchHandler:state.searchHandler,
    isPublisherPicked:state.isPublisherPicked,
  }
}


export default connect(mapStateToProps)(Main);
