import React from 'react';
import Slider from 'react-slick';
import '../css/banner.scss';
import Library from './Library';
import { nanoid } from 'nanoid';
import {Link} from 'react-router-dom';

function Banner(props)  {

const settings = {
  slidesToShow:3,
  autoplay:false,
  autoplaySpeed:3000,
  centerMode:true,
  variableWidth:true,
  autoplay:true,
  autoplaySpeed:3000,
  pauseOnFocus:true,
  pauseOnHover:true,
}

const library=Library;



const arr=library.sort(filt);

function filt(a, b) {
  if (a.year < b.year) return 1; // если первое значение больше второго
  if (a.year == b.year) return 0; // если равны
  if (a.year > b.year) return -1; // если первое значение меньше второго
}

function showBookFull(e){



const vals={
  id:e.currentTarget.dataset.id,
}
localStorage.setItem('pickedBookId', vals.id);

props.dispatch({
  type:'BOOK_WAS_PICKED',
  pickedBook:props.booksToShow[vals.id],
})



}

const slide=arr.map((item,index)=>{
  if(index<6){
  return <Link to="/bookPage" key={nanoid()}><div className="slide"  onClick={showBookFull} data-img={item.img} data-name={item.name} data-publisher={item.publisher} data-id={item.id}>
  <div className="slide-content" >
    
  <img src={item.img} alt="book" className="slide-img"/>
  <div className="content-info_wrap">
  <p className="content-name">{item.name}</p>
  <p className="content-year">{item.year}</p>
  <p className="content-publisher">{item.publisher}</p>
  </div>
  </div>
  </div>
  </Link>
  }
})


return(

<Slider {...settings}>
  {slide}
</Slider>

  )//return
}//banner

export default React.memo(Banner);