import React from 'react';
import '../css/bin.scss';
import {connect} from 'react-redux';
import Library from './Library';





function Bin(props) {

    function deleteFromBin(e){

        let booksCollection=props.booksInBin;
        let addedBook=Library.find((item)=>{
          return item.id==e.currentTarget.dataset.id;
        })
        booksCollection.splice(booksCollection.indexOf(addedBook),1);

        let btnsCollection=props.btnIds;
        btnsCollection.splice(btnsCollection.indexOf(e.currentTarget.dataset.id),1);
        

        props.dispatch({
          type:'BOOK_WAS_DELETED_FROM_BIN',
          booksInBin:booksCollection,
          btnIds:btnsCollection,
          })

        props.forceUpdateHeaderFunc();
        
    }

    if(props.booksInBin.length!=0){
    
    return(
        <div className="bin">
            <div className="bin-container">
{/*--------------------------------------------------  */}
        {props.booksInBin.map((book)=>{
            return <div className="bin-item">
            <img src={book.img} alt={book.name}/>
            <div className="bin-item_info">
                <h1>название: <b>{book.name}</b></h1>
                <h2>издательство: <b>{book.publisher}</b></h2>
                <h3>рейтинг: <b>{book.rating} ★</b></h3>
                <h4>год: <b>{book.year}</b></h4>
                <div>
                    <button className="bin-showbook_btn">подробнее..</button>
                    <button className="bin-delete_btn" onClick={deleteFromBin} data-id={book.id}>удалить</button>
                </div>
            </div>
        </div>
        })}
{/*----------------------------------------------------  */}
            </div>
        </div>
      
  
      )//return
    }else{
        return (
        <div className="bin">
        <div className="bin-container">
            <h1 className="empty-bin-msg">Ваша корзина пуста</h1>
        </div>
        </div>
        )
    }

}//Bin


function mapStateToProps(state) {
    return {
        booksInBin:state.booksInBin,
        toBinBtns:state.toBinBtns,
        btnIds:state.btnIds,
        forceUpdateHeaderFunc:state.forceUpdateHeaderFunc,
    }
}
export default connect(mapStateToProps)(Bin);
