import React from 'react';
import '../css/bookCard.scss';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import AddToBinBtn from './AddToBinBtn';



function BookCard(props) {

  const addToBin=(e)=>{
    

    
    props.forceUpdateHeaderFunc();
    
  }
  

 


  const showBookFull=(e)=>{
    const vals={
      id:e.currentTarget.dataset.id,
      img:e.currentTarget.dataset.img,
      name:e.currentTarget.dataset.name,
      publisher:e.currentTarget.dataset.publisher,
      rating:e.currentTarget.dataset.rating,
    }

    
    


    props.dispatch({
      type:'BOOK_WAS_PICKED',
      pickedBook:props.booksToShow[vals.id],
    })

    localStorage.setItem('pickedBookId', vals.id);


    // props.getBookValues(vals);

  }


 
return(
  
  <>
 
    <div
    className="m-book_card"
    onClick={showBookFull}
    data-id={props.book.id}
    data-img={props.book.img}
    data-name={props.book.name}
    data-publisher={props.book.publisher}
    data-rating={props.book.rating}>
 
    <img
      src={props.book.img}
      alt="картинка"
      className="book-img" />

    <p className="book-name">{props.book.name}</p>
    <p className="book-price">{props.book.price} ₴</p>

    <div className="book-info">
      <p className="book-publish"><span>Издатель:</span><b>{props.book.publisher}</b></p>
      <p className="book-rating"><span>Рейтинг:</span><b>{props.book.rating} ★</b></p>
      <p className="book-year"><span>Год изд.:</span><b>{props.book.year}</b></p>
    </div>
    
    <Link to='/bookPage'>
    <button className="book-btn read_btn">подробнее</button><br/>
    </Link>
    <AddToBinBtn bookid={props.bookid}/>
    </div>

</>

  )//return
}//bookcard

function mapStateToProps(state) {
  return {
    isBookPicked:state.isBookPicked,
    booksToShow:state.booksToShow,
    booksInBin:state.booksInBin,
    forceUpdateHeaderFunc:state.forceUpdateHeaderFunc,
  }
}


export default connect(mapStateToProps)(BookCard);


