import $ from 'jquery';
import '../css/deleteBtn.scss';
import {connect} from 'react-redux';
import Library from './Library';
import React from 'react';

function DeleteBtn(props) {

   const appendToBin=(e)=>{

        
        e.stopPropagation(); 
        
        let booksCollection=props.booksInBin;
        let addedBook=Library.find((item)=>{
          return item.id==e.currentTarget.dataset.id;
        })
        booksCollection.push(addedBook);

        let btnsCollection=props.btnIds;
        btnsCollection.push(e.currentTarget.dataset.id);

        props.dispatch({
          type:'BOOK_WAS_ADDED_TO_BIN',
          booksInBin:booksCollection,
          btnIds:btnsCollection,
          })

        props.forceUpdateHeaderFunc();

        $(e.currentTarget).text('добавлено').addClass('disabled').attr('disabled',true);
    }
    
   


      return (
        <button className="book-btn to-bin_btn" onClick={appendToBin} data-id={props.bookid}>в корзину</button>
      );    
  
}

function mapStateToProps(state) {
  return {
    booksInBin:state.booksInBin,
    booksToShow:state.booksToShow,
    forceUpdateHeaderFunc:state.forceUpdateHeaderFunc,
    btnIds:state.btnIds,
  }
}


export default connect(mapStateToProps)(DeleteBtn);