import React from 'react';
import {connect} from 'react-redux';
import '../css/bookPage.scss';
import Library from '../components/Library';
import $ from 'jquery';




function BookPage(props) {
 
  let unknownUser='Unknown user';

  let commentInp=React.createRef();
  
  let pickedBookId = localStorage.getItem('pickedBookId');
  let pickedBook = Library.find((item)=>{
    return item.id == pickedBookId
  })



  function openBookToRead(){
    alert('sorry, but work of this webpage is still in fixing process');
  }

  function sendComment(e){
    let [...comments]=props.comments;
    e.preventDefault();
    comments.push({
      user: unknownUser,
      comment:$(commentInp.current).val(),
    })
    $(e.currentTarget).css('background','green').text('отправлено').attr('disabled',true);
    props.dispatch({
      type:'BOOK_WAS_COMMENTED',
      comments:comments,
    })
    $(commentInp.current).val('')
  }

  
    return(
      <div className="page">
        <div className="container"> 
          <div className="page-body">
        <aside className="page-aside">
            <img src={pickedBook.img} alt="" className="book-img"/>
            <div className="book-info">
            <h2 className="book-title">{pickedBook.name}</h2>
            <h3 className="book-publisher">Издание: {pickedBook.publisher}</h3>
            <h4 className="book-rating">{pickedBook.rating} ★★★★★</h4>
          </div>
          <div className="page-aside_btns">
            <button className="read-btn btn" onClick={openBookToRead}>читать</button><br/>
          </div>
        </aside>
        <main className="page-main">
      
          
          <div className="book-descr">
            <h1>Описание</h1>
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quisquam quas, obcaecati at deserunt nisi vero quasi eaque, ea cupiditate pariatur ducimus deleniti nesciunt odit quos iusto placeat! Ratione qui sapiente quisquam voluptates iste commodi porro repellat, quos eius optio tempora, molestias suscipit consequuntur ducimus est, dignissimos dolor. Autem tempora error, perferendis magni, voluptatibus saepe dolorem adipisci architecto nisi aliquam odio, earum totam nihil laboriosam quibusdam a. Sint eaque temporibus excepturi quas officiis nulla odio aut ut eius vero fuga exercitationem sunt laboriosam, repellendus ad minima hic quo vitae! At accusamus magni laborum perspiciatis recusandae quod, reprehenderit deserunt quisquam dolorum impedit.
          </div>
        </main>
      
      
        <div className="comments">
      
          <div className="input-holder">
            <h2>Написать отзыв</h2>
            <textarea className="comment-inp" name="name" rows="3" cols="50" ref={commentInp}></textarea>
            <button className="btn" onClick={sendComment}>отправить...</button>
          </div>
      
          <div className="comments-holder">
            <h2>Отзывы читателей</h2>
          {props.comments.map((item)=>{
            return <div className="comment">
            <p className="users-name">{item.user}</p>
            <div className="users-comment">{item.comment}</div> 
            </div>
          })}
            

          </div>
      
      
        </div>
        </div>
        </div> 
      </div>//className page   
      )//return
}//Page


function mapStateToProps(state) {
  return{
    pickedBook:state.pickedBook,
    comments:state.comments,
  }
}

export default connect(mapStateToProps)(BookPage);
