import React, {useState,useEffect} from 'react';
import '../../css/header.scss';
import $  from 'jquery';
import LinkPop from '../LinkPop';
import LoginForm from '../LoginForm';
import RegisterForm from '../RegisterForm';
import Library from '../Library';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';




function Header(props){

    const [value, setValue] = useState(0); // integer state
    function useForceUpdate(){
        setValue(value + 1); // update the state to force render
    }
    useEffect(()=>{
        props.dispatch({
            type:'SENT_FORCE_UPDATE_FUNC',
            forceUpdateHeaderFunc:useForceUpdate,
        })
    })

    

let filteredBooks;

function searchHandler(){

    

    let searchInputValue = $('#searchInput').val();

    if($('#searchInput').val().length==0){
        props.dispatch({
            type:'INPUT_EMPTY',
            typingState:false,
     
        })
    };

    const REX=new RegExp(`${searchInputValue}`,'i');
  
if(props.isPublisherPicked==false){
    filteredBooks=Library.filter((item)=>{
            let res=item.name.match(REX);
            if(res!==null){
            return item;
            }
        })
}else{
    filteredBooks=props.filteredLibrary.filter((item)=>{
        let res=item.name.match(REX);
        if(res!==null){
          return item;
        }
     })
}
    

   props.dispatch({
       type:'SEARCHED',
       books:filteredBooks,
       typingState:true,

   })

}



function showPopUp(e){
    $(e.currentTarget).children('.pop-up').fadeIn();
}

function hidePopUp(e){
    $(e.currentTarget).children('.pop-up').fadeOut();
}

const popUpContent={
    contacts:{
        tel:'+380933796623',
        mail:'mandarinq89@gmail.com',
    },
    address:{
       город:"Киев",
        улица:"Радужная",
        дом:'3б',
    }
}








function showProfile() {

    $('.profile-pop_background').fadeIn();

}





        return(
        <header className="header">
            <LoginForm/>
            <RegisterForm/>
        <div className="h-container container">
            <div className="h-blocks_wrapper">
                <Link to="/" className="h-logo_block"/>


    
        <div className="h-search_block">
        <div className="h-inputs_holder">
        <input id="searchInput" onInput={searchHandler} type="text" className="h-search_inp"/>
        <i className="fas fa-search"></i>
    </div>

    </div>
    
    

                
                <div className="h-btns_container">

                    <Link to="/profPop" className="h-profile_block" onClick={showProfile}>
                        <i className="far fa-user-circle"></i>
                    </Link>
                    <Link to='/bin' className="h-bin_block">
                    <i className="fas fa-shopping-cart"></i>
                    <p className="h-bin-count_disp">{props.booksInBin.length}</p>
                    </Link>
                    
                </div>
                

            </div>
            <nav className="h-navigation_wrapper">
            <Link to="/" className="nav-link">Главная</Link>

            <div className="h-navigation_item" onMouseEnter={showPopUp} onMouseLeave={hidePopUp}>
                <p className="nav-link">Контакты</p>
                <LinkPop linkValues={popUpContent.contacts}/>
                </div>


            <div className="h-navigation_item" onMouseEnter={showPopUp} onMouseLeave={hidePopUp}>
            <p className="nav-link">Адрес</p>
            <LinkPop linkValues={popUpContent.address}/>
            </div>


        </nav>
        </div>
    </header>
        )
}

function mapStateToProps(state) {
    return {
        booksToShow:state.booksToShow,
        isPublisherPicked:state.isPublisherPicked,
        filteredLibrary:state.filteredLibrary,
        isBookPicked:state.isBookPicked,
        booksInBin:state.booksInBin,
    }
}





export default connect(mapStateToProps)(Header);
