import React from 'react';
import $ from 'jquery';
import '../css/loginForm.scss';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';



function LoginForm(props) {


  //  useEffect(() => {
  //   $.ajax({
  //     method: 'GET',
  //     url:'http://192.168.1.125:3001/sendinfo.php',
  //     crossDomain: true,
  //     dataType: 'json',
  //     success: function(data){
  //         users=data;
  //         console.log(users);
  //     }
  // })
  //  })



  function closePop(){
      $('.login-form_background').fadeOut();
      $('.log-login-inp').removeClass('isValidate');
      $('.log-login-inp').removeClass('notValidate');
      $('.log-login-inp').val('');

      $('.log-password-inp').removeClass('isValidate');
      $('.log-password-inp').removeClass('notValidate');
      $('.log-password-inp').val('');

  }

  function logIn(){
      let enteredLogin=$('.log-login-inp').val();
      let enteredPassword=$('.log-password-inp').val();

      if(props.users!=undefined){
        let result=props.users.find((item) =>{
          return item.login === enteredLogin;
        })
        
        if(result){
          $('.log-login-inp').removeClass('notValidate');
          $('.log-login-inp').addClass('isValidate');
  
            if(result.password==enteredPassword){
              $('.log-password-inp').removeClass('notValidate');
              $('.log-password-inp').addClass('isValidate');
              $('.error').text('');
  
            }else{
              $('.log-password-inp').removeClass('isValidate');
              $('.log-password-inp').addClass('notValidate');
              $('.error').text('неправильный пароль');
            }
        }else{
          $('.log-login-inp').removeClass('isValidate');
          $('.log-login-inp').addClass('notValidate');
          $('.log-password-inp').val('');
          $('.log-password-inp').removeClass('isValidate');
          $('.error').text('пользователь не найден');
        }
      }else{
        alert('unfortunately, server is not available');
      }
      


     
      
      
  }

  

    return(
      <div className="login-form_background">
        <div className="login-form_container">
          <div className="login-form">
            <Link to="/" className="close-btn_wrapper" onClick={closePop}>
              <button className="close-btn">закрыть</button>
            </Link>

            <div className="login-content_wrapper">
              <div className="login-titles_wrapper">
                <h1><b>Введите логин и пароль</b></h1>
              </div>
              <div className="login-inputs_wrapper">
                <input className="log-login-inp" type="text" placeholder="логин"/>
                <input className="log-password-inp" type="password" placeholder="пароль"/>
                <button className="login-btn" onClick={logIn}>Войти</button>
                <p className="error"></p>
              </div>



            </div>

          </div>
        </div>
      </div>
      
      )//return

}//Bin

function mapStateToProps(state) {
  return {
    users:state.users,
  }
}

export default connect(mapStateToProps)(LoginForm);


