import React from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import '../css/profPop.scss';




function ProfPop(props) {
  function closePop(){
      $('.profile-pop_background').fadeOut();
  }

  function showLoginForm(){
    $('.profile-pop_background').hide();
    $('.login-form_background').fadeIn();

  }

  function showRegisterForm(){
    $('.profile-pop_background').hide();
    $('.register-form_background').fadeIn();

  }

  

    return(
      <div className="profile-pop_background">
        <div className="profile-pop_container">
          <div className="profile-pop">
            <Link to="/" className="close-btn_wrapper" onClick={closePop}>
              <button className="close-btn">закрыть</button>
            </Link>

            <div className="profile-content_wrapper">
              <div className="profile-titles_wrapper">
                <h1>Добро пожаловать в <br/><b>Comics Online!</b></h1>
                <h2>Войдите чтобы оставить отзыв или оставить коментарий. Нет акаунта? Не проблема, это займет пару минут вашего времени.</h2>
              </div>
              <div className="profile-btns_wrapper">
                <Link to="/loginForm"><button className="profile-logIn_btn" onClick={showLoginForm}>Войти</button></Link>
                <Link to="/registerForm"><button className="profile-register_btn"onClick={showRegisterForm}>Зарегестрироваться</button></Link>
              </div>



            </div>

          </div>
        </div>
      </div>
      
      )//return

}//Bin

export default ProfPop;

