import React, { useState } from 'react';
import { nanoid } from 'nanoid';
import '../css/linkPop.scss';



function LinkPop(props) {

     const li=Object.keys(props).map((outObjKey)=>{
      return   Object.keys(props[outObjKey]).map((inObjKey)=>{
// ----------------------------------------
                 if(inObjKey=='tel'){
                    return <li className="pop-up_item" key={nanoid()}>
                                <a href="tel:+{props[outObjKey][inObjKey]}">{inObjKey+': '+props[outObjKey][inObjKey]}</a>
                            </li>
                 }else if(inObjKey=='mail'){
                    return <li className="pop-up_item" key={nanoid()}>
                             <a href="mailto:{props[outObjKey][inObjKey]}">{inObjKey+': '+props[outObjKey][inObjKey]}</a>
                           </li>
                 }else{
                    return <li className="pop-up_item" key={nanoid()}>{inObjKey+': '+props[outObjKey][inObjKey]}</li> 
                 }
//  ---------------------------------------
            })

        })
    


    return(
        <ul className="pop-up">
            {li}
        </ul>
        
    )//return
}//Page

export default LinkPop;
